html {
    scroll-behavior: smooth;
  }
  
  /* image popup */
  img.mfp-img {
    box-shadow: 0 0 8px rgb(0 0 0 / 60%);
    position: absolute;
    /* max-height: 392px; */
    padding: 0 !important;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  /* .mfp-img-container .mfp-content {
     max-width: 400px !important;
  }*/
  
  .mfp-img-container .mfp-close {
    top: -110px;
    right: -24px;
  }
  
  .contact-form .form-group .form-control.invalid {
    border-color: #ff0000;
  }
  
  
  